var render = function render(){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"curso-main-container pb-3"},[_c('BannerInterno'),_c('div',{staticClass:"container tarjeta tarjeta--blanca p-4 p-md-5 mb-5"},[_vm._m(0),_vm._m(1),_vm._m(2),_c('p',{staticClass:"mt-5 mb-5"},[_vm._v("Siempre existen alternativas de opciones para decidir a nivel organizacional. La organización debe “reaccionar” tomando decisiones en todo momento, en finanzas, en recursos humanos (RRHH), en mercadeo, en producción, Seguridad y Salud en el Trabajo (SST); en fin, todas las áreas corporativas son objeto de toma de decisiones. Ahora bien, la diferencia radica básicamente, en que corporativamente, las tomas de decisiones deben ser planeadas, documentadas, analizadas y, finalmente, accionadas, utilizando herramientas desarrolladas específicamente para lograr bajar el grado de incertidumbre; es bien importante ejecutar las acciones de mejora que resulten de las evaluaciones y esa es una excelente decisión.")]),_c('p',{staticClass:"mt-5 mb-5"},[_vm._v("Los tipos de decisión van muy ligados al tipo de empresa, así como a la magnitud de su ejercicio o de la cantidad de empleados. A continuación, se presentan las más comunes y transversales.")]),_c('div',{staticClass:"row justify-content-center mt-3"},[_vm._m(3),_c('div',{staticClass:"col-md-9"},[_c('TabsC',{staticClass:"color-acento-contenido mt-5"},[_c('div',{staticClass:"tarjeta tarjeta-verde color-contenido p-4 mb-3",attrs:{"titulo":"Decisiones de dirección"}},[_c('div',{staticClass:"row justify-content-center"},[_c('p',[_vm._v("Este tipo de decisiones son las que se realizan en las reuniones directivas, asambleas de dirección, juntas de socios, donde, desde la dirección, se planean normas y políticas a seguir a nivel macro. Estas decisiones generalmente afectan a mediano y largo plazo, el direccionamiento general de la organización.")])])]),_c('div',{staticClass:"tarjeta tarjeta-verde color-contenido p-4 mb-3",attrs:{"titulo":"Decisiones 